@import '../../constants.scss';


.wrapper {
    background: $gradientBackground-color;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: default;

    .skillSections {
        width: 90%;
        padding: 100px 0 100px 0;

        @include mq(tablet) {
            padding: 0;
            width: 80%;
        }

        ul {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            padding: 0 10px;

            li {
                list-style: none;

                figure {
                    margin: 10px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }

                figcaption {
                    text-align: center;
                    font-size: 1.4rem;
                    font-weight: 500;
                    text-transform: uppercase;
                    color: white;
                    text-shadow: 0px 3px 6px #00203F;
                }
            }
        }

        h1 {
            text-align: center;
            color: $cello-color;
            font-size: 3rem;
            text-shadow: 0px 3px 6px white;
            margin: 0;
            padding-top: 30px;
        }
    }

    .footer {
        position: fixed;
        bottom: 0;
        right: 0;
        margin: 5px;

        @include mq(tablet) {
            position: absolute;
            bottom: 0;
            right: 0;
        }

        a {
            text-decoration: none;
            color: $midnight-color;
            font-size: 1.2rem;
            font-weight: 500;
        }

    }
}